@import url(https://fonts.googleapis.com/css?family=Roboto:300);

.nome{
  margin: 0px 0px 15px 0px;
  color: black;
  font-size: 26px;
  text-align: center;
  font-weight: bolder;
}

.if{
  width: 100%;
  height: 100px;
  background: rgba(25,136,44,1);  
}

.if p{
  color: white;
  font-size: 35px;
  font-weight: bold;  
  padding: 60px 10px;
  margin: 0px;
}

.if2{
  width: 100%;
  height: 25px;
  background: rgb(16, 85, 28);
}

.imagem img{
  width:122;
  height:111;
  border-radius: 50%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;  
}
.loginpage {
  width: 360px;
  padding: 10px;
  margin: auto;
  
}
.form {
  position: relative;
  z-index: 1;
  background: green;
  max-width: 360px;
  margin: 0 auto 100px;
  padding: 15px 45px 45px 45px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.form input {
  font-family: "Roboto", sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
}
.form button {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: red;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #FFFFFF;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}
.form button:hover,.form button:active,.form button:focus {
  background: #43A047;
}
.form .message {
  margin: 15px 0 0;
  color: #b3b3b3;
  font-size: 12px;
}

.form .titulo {
  margin: 15px 0;
  color: white;
  font-size: 18px;
  font-weight: bold;
}

.form .message a {
  color: white;
  text-decoration: none;
}
.form .registerform {
  display: none;
}
.container {
  position: relative;
  z-index: 1;
  max-width: 300px;
  margin: 0 auto;
}
.container:before, .container:after {
  content: "";
  display: block;
  clear: both;
}
.container .info {
  margin: 50px auto;
  text-align: center;
}
.container .info h1 {
  margin: 0 0 15px;
  padding: 0;
  font-size: 36px;
  font-weight: 300;
  color: #1a1a1a;
}
.container .info span {
  color: #4d4d4d;
  font-size: 12px;
}
.container .info span a {
  color: #000000;
  text-decoration: none;
}
.container .info span .fa {
  color: #EF3B3A;
}